import History from "history";
import React from "react";
import styled from "react-emotion";
import { Row, Col } from "react-flexbox-grid";
import { Link, withRouter } from "react-router-dom";
import HeaderLink from "./HeaderLink";
import { WindowSizeConsumer } from "../components";

interface Props {
  location: History.Location;
}

const Wrapper = styled("div")`
  margin-top: 50px;
`;

const Logo = styled("span")`
  font-size: 50px;
  a {
    text-decoration: none;
  }
  a:link,
  a:visited,
  a:active {
    color: ${p => p.theme.colors.text};
  }
  a:hover {
    color: ${p => p.theme.colors.primary};
  }
`;

const breakpoint = 768;

const isStartPage = (location: History.Location): boolean =>
  !!location.pathname && location.pathname === "/";

// const isPublisherPage = (location: History.Location): boolean =>
//   !!location.pathname && location.pathname === "/verlag";

const RenderLogo: React.SFC = () => (
  <Col md sm={12}>
    <Logo>
      <Link to="/">INK</Link>
    </Logo>
  </Col>
);

const Header: React.SFC<Props> = ({ location }) =>
  isStartPage(location) ? null : (
    <WindowSizeConsumer>
      {({ width }) => (
        <Wrapper>
          <Row center="xs" middle="xs">
            {width < breakpoint && <RenderLogo />}
            <Col md sm={12}>
              <HeaderLink to={"/neotraditional"}>Neotraditional</HeaderLink>
            </Col>
            <Col md sm={12}>
              <HeaderLink to={"/fineline"}>Fineline</HeaderLink>
            </Col>
            {width >= breakpoint && <RenderLogo />}
            <Col md sm={12}>
              <HeaderLink to={"/aquarelle"}>Aquarelle</HeaderLink>
            </Col>
            <Col md sm={12}>
              <HeaderLink to={"/tattookuenstler"}>Tattookünstler</HeaderLink>
            </Col>
          </Row>
        </Wrapper>
      )}
    </WindowSizeConsumer>
  );

export default withRouter(Header);



