import React from "react";
import { Row } from "react-flexbox-grid";
import {
  ImageData,
  ModalWithImage,
  ImageRow,
  PageHeading,
  Submissions,
  VideosSection
} from "../components";
import { LayoutProps } from "../hoc/withLayout";

import img1 from "../images/fineline/1.jpg";
import img2 from "../images/fineline/2.jpg";
import img3 from "../images/fineline/3.jpg";
import einsendung1 from "../images/einsendungen/fineline/1.jpg";
import einsendung2 from "../images/einsendungen/fineline/2.jpg";
import einsendung3 from "../images/einsendungen/fineline/3.jpg";
import einsendung4 from "../images/einsendungen/fineline/4.jpg";
import einsendung5 from "../images/einsendungen/fineline/5.jpg";
import einsendung6 from "../images/einsendungen/fineline/6.jpg";

const images: ImageData[] = [
  {
    alt: "Fineline Tattoo",
    modalContent: {
      title: '"Blaue Rose" - Tilo Hose',
      description:
        "Das Besondere an diesem Tattoo ist die Farbe: Auch wenn das Blau hier sehr prägnant und auffällig ist, so bildet es doch eine wunderschöne Komposition aus schwarzen Linien, blauen Blüten und ..."
    },
    url: img1
  },
  {
    alt: "Fineline Tattoo",
    modalContent: {
      title: '"Blaue Rose" - Tilo Hose',
      description:
        "Das Besondere an diesem Tattoo ist die Farbe: Auch wenn das Blau hier sehr prägnant und auffällig ist, so bildet es doch eine wunderschöne Komposition aus schwarzen Linien, blauen Blüten und ..."
    },
    url: img2
  },
  {
    alt: "Fineline Tattoo",
    modalContent: {
      title: '"Blaue Rose" - Tilo Hose',
      description:
        "Das Besondere an diesem Tattoo ist die Farbe: Auch wenn das Blau hier sehr prägnant und auffällig ist, so bildet es doch eine wunderschöne Komposition aus schwarzen Linien, blauen Blüten und ..."
    },
    url: img3
  }
];

type Props = LayoutProps;

class Fineline extends React.Component<Props> {
  handleClickImage = (
    imagesIndex: number
  ): React.MouseEventHandler<any> => () => {
    this.props.setModalContent(
      <ModalWithImage
        imageData={images[imagesIndex]}
        handleCloseModal={this.props.handleCloseModal}
      />
    );
    this.props.handleOpenModal();
  };

  render(): React.ReactNode {
    return (
      <React.Fragment>
        <Row center="xs">
          <PageHeading>Fineline</PageHeading>
        </Row>
        <ImageRow
          images={images.map(i => ({ url: i.url }))}
          handleClickImage={this.handleClickImage}
        />
        <VideosSection />
        <Row center="xs">
          <PageHeading>Einsendungen</PageHeading>
        </Row>
        <Submissions
          images={[
            einsendung1,
            einsendung2,
            einsendung3,
            einsendung4,
            einsendung5,
            einsendung6
          ]}
        />
      </React.Fragment>
    );
  }
}

export default Fineline;



