import update from "immutability-helper";

export enum Colors {
  aquarelle = "#0dfadf",
  fineline = "#00c86f",
  neotraditional = "#f47300",
  dark = "#2a2a2a",
  white = "#ffffff"
}

export interface Theme {
  colors: {
    primary: Colors;
    background: Colors;
    text: Colors;
  };
  fonts: {
    size: {
      h1: number;
      h2: number;
      h3: number;
    };
    weight: {
      h1: number;
    };
  };
  boxShadow: string;
}

export const defaultTheme: Theme = {
  colors: {
    primary: Colors.neotraditional,
    background: Colors.dark,
    text: Colors.white
  },
  fonts: {
    size: {
      h1: 70,
      h2: 40,
      h3: 30
    },
    weight: {
      h1: 400
    }
  },
  boxShadow: "box-shadow: 5px 5px 20px 0 black"
};

export const neotraditionalTheme: Theme = update(defaultTheme, {
  colors: {
    primary: { $set: Colors.neotraditional }
  }
});

export const finelineTheme: Theme = update(defaultTheme, {
  colors: {
    primary: { $set: Colors.fineline }
  }
});

export const aquarelleTheme: Theme = update(defaultTheme, {
  colors: {
    primary: { $set: Colors.aquarelle }
  }
});



