import React from "react";
import { Row, Col } from "react-flexbox-grid";
import { ClickableImage } from "../components";
import { Colors } from "../config";

interface Props {
  images: Array<{
    url: string;
    borderColor?: Colors;
  }>;
  handleClickImage?(index: number): React.MouseEventHandler<any>;
}

const ImageRow: React.SFC<Props> = ({ images, handleClickImage }) => (
  <Row around="xs" style={{ marginBottom: "10rem" }}>
    {images.map(({ url, borderColor }, index) => (
      <Col key={index} md={3} xs={5} style={{ paddingTop: "30px" }}>
        <ClickableImage
          src={url}
          onClick={handleClickImage && handleClickImage(index)}
          borderColor={borderColor}
        />
      </Col>
    ))}
  </Row>
);

export default ImageRow;



