import React from "react";
import styled from "react-emotion";
import { Colors } from "../config";

interface ImgProps {
  borderColor?: Colors;
  isClickable?: boolean;
}

const Img = styled<ImgProps, "img">("img")`
  border: 5px solid
    ${p => (p.borderColor ? p.borderColor : p.theme.colors.primary)};
  width: 100%;
  ${p => p.isClickable && `cursor: pointer`};
`;

interface Props {
  src: string;
  onClick?: React.MouseEventHandler<HTMLImageElement>;
  borderColor?: Colors;
}

const ClickableImage: React.SFC<Props> = ({ src, onClick, borderColor }) => (
  <Img
    src={src}
    onClick={onClick}
    borderColor={borderColor}
    isClickable={onClick != null}
  />
);

export default ClickableImage;



