import React from "react";
import { ImageData, ArtistPreview, PageHeading, ImageRow } from "../components";
import { Colors } from "../config";
import { LayoutProps } from "../hoc/withLayout";

import imgTilo from "../images/artists/Tilo.jpg";
import imgBiggi from "../images/artists/Biggi.jpg";
import imgAlex from "../images/artists/Chris.jpg";

interface ArtistData {
  image: ImageData;
  color?: string;
  company: string;
  name: string;
  text: React.ReactNode;
}

const tilo: ArtistData = {
  name: "Tilo",
  color: Colors.neotraditional,
  company: "Electric Traditionals",
  text: (
    <div>
      <p>
        Auch die Farbübergänge wurden weicher und Schattierungen intensiver.
        Verbindet man nun diese beiden Stile mit dem Realistic, welcher – wie
        der Name bereits vermuten lässt – Tattoos mit realistischer Darstellung
        enthält, so erhält man den Neotraditional-Stil.
      </p>
      <p>
        Durch diese besondere Form der Interpretation dreier Tattoo-Stile erhält
        man eine zeitlose moderne Zeichenrichtung, die mittlerweile fester
        bestandteil der Tattooszene ist.
      </p>
    </div>
  ),
  image: {
    alt: "Tattookünstler",
    border: Colors.neotraditional,
    modalContent: {
      description: "some text",
      title: "some title"
    },
    url: imgTilo
  }
};

const biggi: ArtistData = {
  name: "Biggi",
  color: Colors.aquarelle,
  company: "Mint Velvet Tattoo",
  text: (
    <div>
      <p>
        Auch die Farbübergänge wurden weicher und Schattierungen intensiver.
        Verbindet man nun diese beiden Stile mit dem Realistic, welcher – wie
        der Name bereits vermuten lässt – Tattoos mit realistischer Darstellung
        enthält, so erhält man den Neotraditional-Stil.
      </p>
      <p>
        Durch diese besondere Form der Interpretation dreier Tattoo-Stile erhält
        man eine zeitlose moderne Zeichenrichtung, die mittlerweile fester
        bestandteil der Tattooszene ist.
      </p>
    </div>
  ),
  image: {
    alt: "Tattookünstler",
    border: Colors.aquarelle,
    modalContent: {
      description: "some text",
      title: "some title"
    },
    url: imgBiggi
  }
};

const alex: ArtistData = {
  name: "Chris",
  color: Colors.fineline,
  company: "Stecher Tattoo",
  text: (
    <div>
      <p>
        Auch die Farbübergänge wurden weicher und Schattierungen intensiver.
        Verbindet man nun diese beiden Stile mit dem Realistic, welcher – wie
        der Name bereits vermuten lässt – Tattoos mit realistischer Darstellung
        enthält, so erhält man den Neotraditional-Stil.
      </p>
      <p>
        Durch diese besondere Form der Interpretation dreier Tattoo-Stile erhält
        man eine zeitlose moderne Zeichenrichtung, die mittlerweile fester
        bestandteil der Tattooszene ist.
      </p>
    </div>
  ),
  image: {
    alt: "Tattookünstler",
    border: Colors.fineline,
    modalContent: {
      description: "some text",
      title: "some title"
    },
    url: imgAlex
  }
};

// const images = [tilo.image, biggi.image, alex.image];
const artists = [biggi, tilo, alex];

type Props = LayoutProps;

const Artists: React.SFC<Props> = () => (
  <React.Fragment>
    <PageHeading>Tattookünstler</PageHeading>
    <ImageRow
      images={artists.map(a => ({
        url: a.image.url,
        borderColor: a.image.border
      }))}
    />
    {artists.map((artist, index) => (
      <ArtistPreview
        imageData={artist.image}
        heading={artist.name}
        subHeading={artist.company}
        color={artist.color}
        imageAlign={index % 2 === 0 ? "left" : "right"}
      >
        {artist.text}
      </ArtistPreview>
    ))}
  </React.Fragment>
);

export default Artists;



