import React from "react";
import styled from "react-emotion";
import { Link, Route, RouteComponentProps } from "react-router-dom";

interface WrapperProps {
  isActive: boolean;
}

interface Props {
  to: string;
}

const LinkWrapper = styled<WrapperProps, "div">("div")`
  padding: 10px 20px;
  font-size: 20px;
  a {
    text-decoration: none;
    &:link,
    &:visited,
    &:active {
      color: ${p =>
        p.isActive ? p.theme.colors.primary : p.theme.colors.text};
    }
  }
`;

const HeaderLink: React.SFC<Props> = ({ to, children }) => {
  const Inner: React.ReactNode = ({
    match,
    location
  }: RouteComponentProps<boolean>) =>
    location.pathname === "/" ? null : (
      <LinkWrapper isActive={!!match}>
        <Link to={to}>{children}</Link>
      </LinkWrapper>
    );
  return <Route path={to} children={Inner} />;
};

export default HeaderLink;



