import React from "react";
import { debounce } from "lodash";

const { Provider, Consumer: WindowSizeConsumer } = React.createContext<State>({
  height: 0,
  width: 0
});

interface State {
  readonly height: number;
  readonly width: number;
}

class WindowSizeProvider extends React.Component<{}, State> {
  static getWindowHeight = () =>
    window.innerHeight ||
    document.documentElement.clientHeight ||
    document.body.clientHeight;
  static getWindowWidth = () =>
    window.innerWidth ||
    document.documentElement.clientWidth ||
    document.body.clientWidth;

  constructor(props: {}) {
    super(props);

    this.state = {
      height: WindowSizeProvider.getWindowHeight(),
      width: WindowSizeProvider.getWindowWidth()
    };

    this.setSizes = debounce(this.setSizes, 200);
  }

  componentDidMount(): void {
    this.setSizes();
    window.addEventListener("resize", this.setSizes);
  }

  componentWillUnmount(): void {
    window.removeEventListener("resize", this.setSizes);
  }

  setSizes = (): void => {
    this.setState(state => {
      const currentHeight = WindowSizeProvider.getWindowHeight();
      const currentWidth = WindowSizeProvider.getWindowWidth();
      if (state.height === currentHeight && state.width === currentWidth) {
        return null;
      }
      return {
        height: currentHeight,
        width: currentWidth
      };
    });
  };

  render(): React.ReactNode {
    return <Provider value={this.state}>{this.props.children}</Provider>;
  }
}

export { WindowSizeConsumer };
export default WindowSizeProvider;



