import React from "react";
import styled from "react-emotion";
import { Grid, Row, Col } from "react-flexbox-grid";
import { HomeLink, WindowSizeConsumer } from "../components";

const Heading = styled("h1")`
  font-size: 10rem;
`;

const Home: React.SFC = () => (
  <WindowSizeConsumer>
    {({ height }) => (
      <Grid fluid>
        <Row center="xs" middle="xs" style={{ height: `${height}px` }}>
          <Col xs>
            <Row center="xs">
              <Col>
                <Heading>INK</Heading>
              </Col>
            </Row>
            <Row center="xs">
              <Col md={3}>
                <HomeLink to="/fineline" colorType="fineline">
                  Fineline
                </HomeLink>
              </Col>
              <Col md={3}>
                <HomeLink to="/neotraditional" colorType="neotraditional">
                  Neotraditional
                </HomeLink>
              </Col>
              <Col md={3}>
                <HomeLink to="/aquarelle" colorType="aquarelle">
                  Aquarelle
                </HomeLink>
              </Col>
            </Row>
          </Col>
        </Row>
      </Grid>
    )}
  </WindowSizeConsumer>
);

export default Home;



