import React, { Component } from "react";
import { BrowserRouter } from "react-router-dom";
import { Routes, WindowSizeProvider } from "./components";

class App extends Component<{}, {}> {
  render() {
    return (
      <WindowSizeProvider>
        <BrowserRouter>
          <Routes />
        </BrowserRouter>
      </WindowSizeProvider>
    );
  }
}

export default App;



