import React from "react";
import styled from "react-emotion";
import { Row, Col } from "react-flexbox-grid";

const placeholderText = `
Vorab wollen wir uns aber erst ein mal genau anschauen, was das
Neotraditional Tattoo überhaupt ausmacht. Im Grunde ist der Stil eine
moderne Zusammensetzung aus dem Oldschool, Newschool und dem
Realistic-Tattoo. Wie der Name bereits vermuten lässt, handelt es sich beim
Oldschool &ndash; auch Traditional genannt &ndash; um die hier älteste Tattoorichtung. Am
verbreitetsten sind hier wohl die Seefahrer-Motive, wie z.B. Anker, Kanonen
und Segelschiffe. Von der Gestaltung weisen sie eine markante schwarze
Umrandungslinie auf, die man auch als Outline bezeichnet. Aus dem
Oldschool entwickelte sich daraus mit der Zeit der Newschool-Stil, der die
`;

const VideoWrapper = styled("div")`
  border: 5px solid ${p => p.theme.colors.primary};
  ${p => p.theme.boxShadow};
`;

const InnerWrapper = styled("div")`
  position: relative;
  height: 0;
  padding-bottom: 56.25%;
`;

const IFrame = styled("iframe")`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
`;

const P = styled("p")`
  padding: 1rem;
`;

const VideoSection: React.SFC = () => (
  <React.Fragment>
    <Row middle="xs" style={{ marginBottom: "2rem" }}>
      <Col md={6} sm={12}>
        <VideoWrapper>
          <InnerWrapper>
            <IFrame
              src="https://alugha.com/embed/web-player?v=bdcd057a-6353-11e8-a7bf-5b50a172a331"
              allowFullScreen
            />
          </InnerWrapper>
        </VideoWrapper>
      </Col>
      <Col md={6} sm={12}>
        <P>{placeholderText}</P>
      </Col>
    </Row>
    <Row middle="xs" end="md">
      <Col md={6} sm={12}>
        <P>{placeholderText}</P>
      </Col>
      <Col md={6} sm={12}>
        <VideoWrapper>
          <InnerWrapper>
            <IFrame
              src="https://alugha.com/embed/web-player?v=0621acaf-6353-11e8-b2fc-f70bf6deaf31"
              allowFullScreen
            />
          </InnerWrapper>
        </VideoWrapper>
      </Col>
    </Row>
    <Row middle="xs" style={{ marginBottom: "5rem", marginTop: "2rem" }}>
      <Col md={6} sm={12}>
        <VideoWrapper>
          <InnerWrapper>
            <IFrame
              src="https://alugha.com/embed/web-player?v=f4afe966-6353-11e8-b728-6d45d79368fe"
              allowFullScreen
            />
          </InnerWrapper>
        </VideoWrapper>
      </Col>
      <Col md={6} sm={12}>
        <P>{placeholderText}</P>
      </Col>
    </Row>
  </React.Fragment>
);

export default VideoSection;



