import React from "react";
import { Subtract } from "utility-types";
import styled from "react-emotion";
import { withTheme } from "emotion-theming";
import Modal from "react-modal";
import { Grid } from "react-flexbox-grid";
import { Header, Footer } from "../components";
import { Theme } from "../config";

interface CustomBackgroundOverlayProps {
  modalIsOpen: boolean;
}

const CustomBackgroundOverlay = styled<CustomBackgroundOverlayProps, "div">(
  "div"
)`
  position: fixed;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  background-color: ${p => p.theme.colors.background};
  opacity: ${p => (p.modalIsOpen ? 0.5 : 0)};
  z-index: ${p => (p.modalIsOpen ? 10 : -10)};
  transition: opacity 0.1s ease-in;
`;

export interface LayoutProps {
  handleOpenModal(): void;
  handleCloseModal(): void;
  setModalContent(node: React.ReactNode): void;
}

const withLayout = <WrappedProps extends LayoutProps>(
  WrappedComponent: React.ComponentType<WrappedProps>
) => {
  type HocProps = Subtract<WrappedProps, LayoutProps> & { theme: Theme };

  interface HocState {
    readonly modalContent: React.ReactNode;
    readonly showModal: boolean;
  }

  class WithLayout extends React.PureComponent<HocProps, HocState> {
    static displayName = `withLayout(${WrappedComponent.name})`;
    static readonly WrappedComponent = WrappedComponent;

    readonly state: HocState = {
      modalContent: null,
      showModal: false
    };

    setModalContent = (modalContent: React.ReactNode): void => {
      this.setState({ modalContent });
    };

    handleOpenModal = (): void => {
      this.setState({ showModal: true });
    };

    handleCloseModal = (): void => {
      this.setState({ showModal: false });
    };

    render() {
      return (
        <React.Fragment>
          <CustomBackgroundOverlay modalIsOpen={this.state.showModal} />
          <Grid style={{ zIndex: 0 }}>
            <Header />
            <WrappedComponent
              handleOpenModal={this.handleOpenModal}
              handleCloseModal={this.handleCloseModal}
              setModalContent={this.setModalContent}
            />
            <Footer />
            <Modal
              onRequestClose={this.handleCloseModal}
              isOpen={this.state.showModal}
              style={{
                content: {
                  color: this.props.theme.colors.text,
                  backgroundColor: this.props.theme.colors.background,
                  border: "0px",
                  top: "100px",
                  left: "0",
                  right: "0",
                  bottom: "0"
                },
                overlay: {
                  backgroundColor: "transparent",
                  zIndex: 20
                }
              }}
            >
              {this.state.modalContent}
            </Modal>
          </Grid>
        </React.Fragment>
      );
    }
  }
  return withTheme(WithLayout);
};

export default withLayout;



