import React from "react";
import { Grid, Row, Col } from "react-flexbox-grid";
import styled from "react-emotion";
import { Colors } from "../config";

const ImgWrapper = styled("div")`
  margin: 2em;
`;

const Img = styled("img")`
  border: 5px solid ${p => p.theme.colors.primary};
  width: 100%;
  ${p => p.theme.boxShadow};
`;

const CloseButton = styled("button")`
  font-size: 1.5rem;
  font-weight: bold;
  background: transparent;
  border: none;
  cursor: pointer;
  color: ${p => p.theme.colors.primary};
`;

export interface ImageData {
  url: string;
  alt?: string;
  border?: Colors;
  modalContent?: {
    title: string;
    description: React.ReactNode;
  };
}

export interface Props {
  imageData: ImageData;
  handleCloseModal(): void;
}

class ModalWithImage extends React.Component<Props> {
  render(): React.ReactNode {
    return (
      <React.Fragment>
        <Grid
          style={{
            paddingTop: "20px"
          }}
        >
          <Row end="xs">
            <Col xs>
              <CloseButton onClick={this.props.handleCloseModal}>X</CloseButton>
            </Col>
          </Row>
          <Row middle="xs">
            <Col md={6} xs={12}>
              <ImgWrapper>
                <Img
                  src={this.props.imageData.url}
                  alt={this.props.imageData.alt}
                />
              </ImgWrapper>
            </Col>
            <Col md={6} xs={12}>
              <h2>
                {this.props.imageData.modalContent &&
                  this.props.imageData.modalContent.title}
              </h2>
              <p>
                {this.props.imageData.modalContent &&
                  this.props.imageData.modalContent.description}
              </p>
            </Col>
          </Row>
        </Grid>
      </React.Fragment>
    );
  }
}

export default ModalWithImage;



